<template>
  <div class="consumer">
    <div class="button-group">
      <span class="route-name">{{$route.meta.title}}</span>
      <div class="inline-block ml-10" v-if="tableVal.length !== 0">
        <el-button type="danger" size="small" @click="stopBtn()">禁用</el-button>
        <el-button type="primary" size="small" @click="startBtn()">启用</el-button>
        <el-button type="primary" size="small" @click="demotionBtn()">降级</el-button>
        <el-button type="warning" size="small" @click="demotionRecover()">降级恢复</el-button>
      </div>
    </div>
    <el-form :model="serviceForm" ref="serviceForm" :rules="inputRules" label-width="92px" label-position="right" size="small">
      <el-row class="label-first" :gutter="20">
        <el-col :xl="4" :lg="6">
          <el-form-item :label="$t('redis.redisCluster.clusterName')+'：'" prop="zkClusterId">
            <el-select v-model="serviceForm.zkClusterId" clearable :placeholder="$t('common.pleaseChoose')" style="width:100%">
              <el-option
                v-for="(item) in clusterOption"
                :key="item.id"
                :label="item.pkDesc"
                :value="item.pkValue">
              </el-option>
            </el-select>
          </el-form-item>
        </el-col>
        <el-col :xl="4" :lg="6">
          <el-form-item :label="$t('service.serviceList.serviceName')+'：'" label-width="68px">
            <el-input type="text" v-model.trim="serviceForm.serName" clearable></el-input>
          </el-form-item>
        </el-col>
        <el-col :xl="4" :lg="6">
          <el-form-item :label="$t('service.serviceList.applyName')+'：'" label-width="68px">
            <el-input type="text" v-model.trim="serviceForm.appName" clearable></el-input>
          </el-form-item>
        </el-col>
        <el-col :xl="4" :lg="6">
          <el-form-item :label="$t('service.provider.ip')+'：'" label-width="40px">
            <el-input type="text" v-model.trim="serviceForm.ip" clearable></el-input>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="24" align="right">
          <el-form-item label-width="0px">
            <el-button size="small" type="primary" icon="el-icon-search" @click="search()">{{$t('common.search')}}</el-button>
          </el-form-item>
        </el-col>
      </el-row>
    </el-form>
    <!-- 表格部分 -->
    <el-table stripe :data="consumerList[currentPage - 1]" v-loading="loading" ref="multipleTable" @selection-change="chooseBox" style="width: 100%;">
      <el-table-column type="selection" width="55">
      </el-table-column>
      <!-- 服务名 -->
      <el-table-column align="center" prop="serName" width="400" :label="$t('service.serviceList.serviceName')">
      </el-table-column>
      <el-table-column align="center" prop="appName" :label="$t('service.serviceList.applyName')" width="120">
        <template slot-scope="scope">
          {{ scope.row.appName | filterEmpty}}
        </template>
      </el-table-column>
      <el-table-column align="center" prop="ip" :label="$t('service.provider.ip')" width="150">
      </el-table-column>
      <el-table-column align="center" prop="serStatus" :label="$t('service.provider.status')" width="90">
        <template slot-scope="scope">
          <el-tag :type="scope.row.serStatus ==='true' ? 'success' : 'danger'">{{scope.row.serStatus | serStatus}}</el-tag>
        </template>
      </el-table-column>
      <el-table-column align="center" prop="mockStatus" :label="$t('service.provider.demotion')" width="90">
        <template slot-scope="scope">
          {{scope.row.mockStatus | mockStatus}}
        </template>
      </el-table-column>
      <el-table-column align="center" prop="version" :label="$t('redis.redisList.version')" width="80">
      </el-table-column>
      <el-table-column align="center" prop="serGrop" :label="$t('service.provider.grouping')" width="70">
      </el-table-column>
      <el-table-column width="140" :label="$t('common.operation')" fixed="right">
        <template slot-scope="props">
          <el-button v-if="props.row.serStatus !== 'true'" type="text" size="mini" icon="fa fa-play" @click="startBtn(props.row)" style="color:	#67c23a">启用</el-button>
          <el-button v-if="props.row.serStatus === 'true'" type="text" size="mini" icon="fa fa-stop" @click="stopBtn(props.row)" style="color:	#FF0000">禁用</el-button>
          <el-button v-if="props.row.mockStatus === '0'" type="text" size="mini" icon="fa fa-long-arrow-down" @click="demotionBtn(props)" style="color:	#8B7D7B">降级</el-button>
          <el-button v-if="props.row.mockStatus !== '0'" type="text" size="mini" icon="fa fa-long-arrow-up" @click="demotionRecover(props)" style="color:	#67c23a">降级恢复</el-button>
        </template>
      </el-table-column>
    </el-table>
    <!-- 翻页部分 -->
    <div class="block">
      <el-pagination background @current-change="handleCurrentChange" :current-page="currentPage" :page-size="10" layout="total, prev, pager, next, jumper" :total="consumerTotal">
      </el-pagination>
    </div>
    <!-- 弹出框 -->
    <el-dialog :title="$t('service.provider.demotion')" @close="serviceCancel()" :visible.sync="popBox" width="23%" center>
      <el-form :model="popForm" ref="popForm" label-width="120px" size="mini" status-icon :rules="inputRules">
        <!-- 降级的配置 -->
        <el-form-item :label="$t('service.provider.demotion')+'：'" prop="demotion">
          <el-select v-model="popForm.demotion" clearable :placeholder="$t('common.pleaseChoose')">
            <el-option v-for="(item) in classify.demotionGroup" :key="item.id" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="serviceOk()" size="small">{{$t('common.ok')}}</el-button>
        <el-button @click="serviceCancel()" size="small">{{$t('common.cancel')}}</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import { mapState, mapActions } from 'vuex'
export default {
  name: 'provider',
  data() {
    return {
      loading: false,
      currentPage: 1, // 当前页数
      // 查询规则
      inputRules: {
        zkClusterId: [
          { required: true, message: '请输入集群名称', trigger: 'blur' }
        ],
        demotion: [
          { required: true, message: '请选择降级', trigger: 'blur' }
        ]
      },
      // 查询模板
      serviceForm: {
        zkClusterId: '',
        appName: '',
        ip: '',
        serName: ''
      },
      // 获得表格多选按钮
      tableVal: [],
      serviceActive: '', // single:单个  more：多个
      // 弹出框
      popBox: false,
      popIndex: '', // 表格下标
      popForm: {
        demotion: '' // 降级
      },
      modalData: null
    }
  },
  computed: {
    ...mapState({
      consumerList: state => state.service.consumerList,
      consumerTotal: state => state.service.consumerTotal,
      classify: state => state.service.classify,
      clusterOption: state => state.service.clusterOption
    })
  },
  activated() {
    const vm = this
    if (this.clusterOption.length <= 0) {
      this.initializeData({vm})
    }
    let {zkClusterId} = vm.$route.params
    if (zkClusterId) {
      vm.serviceForm = Object.assign({}, {zkClusterId, 'serName': vm.$route.params.name})
      vm.search()
    }
  },
  methods: {
    ...mapActions('service', ['initializeData', 'serviceFind', 'findConsumer', 'serviceChangeOne', 'consumerStatus', 'serviceDemotion', 'reDemotion']),
    // 翻页事件
    handleCurrentChange(val) {
      this.currentPage = val
    },
    // 查询
    search() {
      if (!this.serviceForm.zkClusterId) {
        this.$message({
          message: '集群名称不能为空！',
          type: 'warning'
        })
        return
      }
      this.loading = true
      this.findConsumer([this, this.serviceForm])
    },
    // 拿到选择的内容
    chooseBox(val) {
      this.tableVal = val
    },
    // 禁用
    stopBtn(props) {
      if (props) {
        const params = {
          serviceName: props.serName,
          zkClusterId: this.serviceForm.zkClusterId,
          consumerHost: props.ip
        }
        this.makeTrue('禁用').then(() => {
          this.consumerStatus([this, params, 'disable'])
        })
        return
      }
      if (!props) {
        const params = {
          serviceName: [],
          consumerHost: [],
          zkClusterId: this.serviceForm.zkClusterId
        }
        this.tableVal.forEach(item => {
          params.serviceName.push(item.serName)
          params.consumerHost.push(item.ip)
        })
        params.serviceName = params.serviceName.toString()
        params.consumerHost = params.consumerHost.toString()
        this.makeTrue('禁用').then(() => {
          this.consumerStatus([this, params, 'disable'])
        })
      }
    },
    // 启用
    startBtn(props) {
      if (props) {
        const params = {
          serviceName: props.serName,
          zkClusterId: this.serviceForm.zkClusterId,
          consumerHost: props.ip
        }
        this.makeTrue('启用').then(() => {
          this.consumerStatus([this, params, 'enable'])
        })
      }
      if (!props) {
        const params = {
          serviceName: [],
          consumerHost: [],
          zkClusterId: this.serviceForm.zkClusterId
        }
        this.tableVal.forEach(item => {
          params.serviceName.push(item.serName)
          params.consumerHost.push(item.ip)
        })
        params.serviceName = params.serviceName.toString()
        params.consumerHost = params.consumerHost.toString()
        this.makeTrue('启动').then(() => {
          this.consumerStatus([this, params, 'enable'])
        })
      }
    },
    // 降级
    demotionBtn(props) {
      this.popForm.demotion = '' // 清空
      this.popBox = true
      if (props) {
        this.serviceActive = 'single'
        this.modalData = { ...props.row }
        this.popIndex = props.$index
      }
      if (!props) {
        this.popForm.demotion = ''
        this.serviceActive = 'more'
      }
    },
    // 降级恢复
    demotionRecover(props) {
      if (props) {
        const params = {
          serName: props.row.serName,
          ip: props.row.ip,
          zkClusterId: this.serviceForm.zkClusterId
        }
        this.makeTrue('恢复降级').then(() => {
          this.reDemotion([this, params])
        })
      } else {
        // 批量
        const params = {
          zkClusterId: this.serviceForm.zkClusterId,
          mock: this.popForm.demotion,
          serName: [],
          ip: []
        }
        this.tableVal.forEach(item => {
          params.serName.push(item.serName)
          params.ip.push(item.ip)
        })
        params.serName = params.serName.toString()
        params.ip = params.ip.toString()
        this.makeTrue('恢复降级').then(() => {
          this.reDemotion([this, params])
        })
      }
    },
    // 弹出框确定
    serviceOk() {
      // 降级
      this.$refs['popForm'].validate(valid => {
        if (valid) {
          if (this.serviceActive === 'single') {
            const params = {
              zkClusterId: this.serviceForm.zkClusterId,
              serName: this.modalData.serName,
              ip: this.modalData.ip,
              mock: this.popForm.demotion
            }
            this.serviceDemotion([this, params])
          }
          if (this.serviceActive === 'more') {
            const params = {
              zkClusterId: this.serviceForm.zkClusterId,
              mock: this.popForm.demotion,
              serName: [],
              ip: []
            }
            this.tableVal.forEach(item => {
              params.serName.push(item.serName)
              params.ip.push(item.ip)
            })
            params.serName = params.serName.toString()
            params.ip = params.ip.toString()
            this.serviceDemotion([this, params])
          }
        }
      })
    },
    // 弹出框取消
    serviceCancel() {
      this.$refs.popForm.resetFields()
      this.popBox = false
    },
    // 确定工具
    makeTrue(words) {
      const vm = this
      return new Promise(function (resolve, reject) {
        vm.$confirm('是否' + words, '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          resolve()
        }).catch(() => {
          vm.$message({
            type: 'info',
            message: '已取消该操作'
          })
        })
      })
    }
  }
}
</script>

<style type="text/scss" lang="scss" scoped>
</style>



